<template>
  <view style="position: relative;">
	  <!-- <popup></popup> -->
	  <el-breadcrumb separator-class="el-icon-arrow-right">
	    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
	    <el-breadcrumb-item>任务管理</el-breadcrumb-item>
		<el-breadcrumb-item>{{tableData[0].GName}}</el-breadcrumb-item>
	  </el-breadcrumb>
	  
	  <el-row :gutter="15" style='height: 80rpx; line-height: 70rpx;'>
	    <el-col :span="1.2"><div class="grid-content el-col-title-action">姓名：</div></el-col>
	    <el-col :span="4"><div class="grid-content el-col-text-action"><input type="text" class="el-col-input-action" @input="userName"/></div></el-col>
	  		<el-col :span="1.2"><div class="grid-content el-col-title-action">手机号：</div></el-col>
	  		<el-col :span="4"><div class="grid-content el-col-text-action"><input type="text" class="el-col-input-action" @input="userPhone"/></div></el-col>
	  		<el-col :span="1.2"><div class="grid-content el-col-title-action">认证：</div></el-col>
	  		<el-col :span="4"><div class="grid-content el-col-text-action"><input type="text" class="el-col-input-action" @input="userAuthentication"/></div></el-col>
	  		<el-col :span="1.2"><div class="grid-content el-col-title-action">时间：</div></el-col>
	  		<el-col :span="4"><div class="grid-content el-col-text-action"><input type="text" class="el-col-input-action" @input="registerTime"/></div></el-col>
	  		<el-col :span="3"><el-button round @click='getfilterdata' style='color: #56585a; background-color: #ffffff; width: 150rpx; height: 70rpx; line-height: 25rpx; text-align: center; padding: 0;'>搜索</el-button></el-col>
	  </el-row>
	  
	  <el-table
	    :data="tableData"
		:show-header='false'
	    style="width: 100%">
		<el-table-column
		  label="序号"
		  width="160">
		  <template slot-scope="scope">
		    <i class="el-icon-collection-tag"></i>
		    <span style="margin-left: 10px">{{ scope.row.id | format_str }}</span>
		  </template>
		</el-table-column>
		</el-table-column>
		<el-table-column
		  label="手机号"
		  width="200">
		  <template slot-scope="scope">
		    <i class="el-icon-data-analysis"></i>
		    <span style="margin-left: 10px">{{ scope.row.UPhone }}</span>
		  </template>
		</el-table-column>
	    <el-table-column
	      label="用户名"
	      width="160">
	      <template slot-scope="scope">
	        <el-popover trigger="hover" placement="top">
	          <p>姓名: {{ scope.row.UName }}</p>
	          <!-- <p>住址: {{ scope.row.address }}</p> -->
	          <div slot="reference" class="name-wrapper">
	            <el-tag size="medium">{{ scope.row.UName }}</el-tag>
	          </div>
	        </el-popover>
	      </template>
	    </el-table-column>
		<el-table-column
		  label="成员定位"
		  width="200">
		  <template slot-scope="scope">
		    <i class="el-icon-map-location"></i>
		    <span style="margin-left: 10px">{{ scope.row.Ulongitude | format_coordinate(scope.row.Ulatitude) }}</span>
		  </template>
		</el-table-column>
		<el-table-column
		  label="用户等级"
		  width="120">
		  <template slot-scope="scope">
		    <i class="el-icon-pie-chart"></i>
		    <span style="margin-left: 10px">{{ scope.row.UGrade }}</span>
		  </template>
		</el-table-column>
		<el-table-column
		  label="消防认证"
		  width="160">
		  <template slot-scope="scope">
		    <i class="el-icon-data-line"></i>
		    <span style="margin-left: 10px">{{ scope.row.UAuthentication | format_Authen }}</span>
		  </template>
		</el-table-column>
		<el-table-column
		  label="创建日期"
		  width="240">
		  <template slot-scope="scope">
		    <i class="el-icon-time"></i>
		    <span style="margin-left: 10px">{{ scope.row.URegistrationTime | format_time }}</span>
		  </template>
		</el-table-column>
	    <el-table-column label="操作">
	      <template slot-scope="scope">
	        <el-button
	          size="mini"
	          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
	        <el-button
	          size="mini"
	          type="danger"
	          @click="getuserId(scope.$index, scope.row)"
			  
	          >删除</el-button>
	        <el-dialog
	          title="提示"
	          :visible.sync="dialogVisible"
	          width="30%">
	          <span>请确认是否将本成员移出群组？</span>
	          <span slot="footer" class="dialog-footer">
	            <el-button @click="dialogVisible = false">取 消</el-button>
	            <el-button type="primary" @click="handleDelete(scope.$index, scope.row)">确 定</el-button>
	          </span>
	        </el-dialog>
	      </template>
	    </el-table-column>
	  </el-table>
	 <view style="padding-top: 30px;">
		 <el-pagination
		   background
		   layout="pager"
		   @current-change="handleCurrentChange"
		   :total="datanum">
		 </el-pagination>
	 </view>
  </view>
</template>

<script>
	import moment from 'moment'
	import popup from '@/components/popup/popup.vue'
  export default {
    data() {
      return {
		token:'',
		AdmId:'',
		dialogVisible: false,
        tableData: [],
		datanum: '',
		uid:'',
		username: '',
		userphone: '',
		userauthentication: '',
		registertime: '',
      }
    },
	props: [
		'gid',
	],
	mounted() {
	  this.getdata(1)
	},
    methods: {
	  // 监听input
	  userName: function(res){
		  this.username = res.detail.value
	  },
	  userPhone: function(res){
		  this.userphone = res.detail.value
	  },
	  userAuthentication: function(res){
	  	  this.userauthentication = res.detail.value
	  },
	  registerTime: function(res){
		  this.registertime = res.detail.value
	  },
		
      handleEdit(index, row) {
        console.log(index, row);
		this.$emit('openpopup')
      },
      handleDelete() {
		this.dialogVisible = false;
		this.reuser(this.uid)
      },
	  handleCurrentChange(index){
		  this.getdata(index)
	  },
	  handleClose(done) {   // 关闭提示
	    this.$confirm('确认关闭？')
	      .then(_ => {
	        done();
	      })
	      .catch(_ => {});
	  },
	  getuserId(index, row) {
		  this.dialogVisible = true
		  this.uid = row.id
	  },
	  // 用户初次请求数据
	  getdata(index) {
			try {
				this.token = uni.getStorageSync('token')
				this.AdmId = uni.getStorageSync('AdmId')
			} catch(e) {
			}
	  		uni.request({
	  			method: 'GET',
				url: this.$store.state.url + 'usersadmin/' + this.gid + '/groupUser/',
				header: {
					'Content-Type' : 'application/json',
					'Authorization': 'JWT ' + this.token,
					'AdmId': this.AdmId,
				},
				success: (ret) => {
					console.log(ret.data)
					this.tableData = ret.data.results
					this.datanum = ret.data.count
				}
	  		})
	  },
	  // 筛选信息
	  getfilterdata() {
		  console.log(this.username)
		  console.log(this.userphone)
		  console.log(this.userauthentication)
		  console.log(this.registertime)
	  },
	  reuser(id) {
		  console.log(id)
		  console.log(this.token)
	  	  uni.request({
	  	  	  method: 'POST',
	  	  	  url: this.$store.state.url + 'userChange/',
			  header: {
			  	'Content-Type' : 'application/json',
			  	'Authorization': 'JWT ' + this.token,
			  	'AdmId': this.AdmId,
			  },
			  data: {
				 'TID':'exit'
			  },
	  	  	  success: (ret) => {
				  console.log(ret)
	  	  	  	  console.log('删除成功')
	  	  	  }
	  	  })
	  }
    },
	components: {
		popup
	},
	filters: {
	  format_str(id) {  // id过滤器
	  	  var strId = String(id)
		  var len = 7 - strId.length
		  var em = '0'
		  for(var i=1; i<len; i++) {
		  	em+='0'
		  }
		  
	  	  return em + strId
	  },
	  format_coordinate(lat, lng) {  // 坐标过滤器
		  lat = parseFloat(lat).toFixed(2)
		  lng = parseFloat(lng).toFixed(2)
		  return '( ' + lat + ', ' + lng + ')'
	  },
	  format_Authen(flag) {
		  if(flag){
			  return '认证通过'
		  }else {
			  return '未认证'
		  }
	  },
	  format_time(time) {  // 时间过滤器
		 return moment(new Date(time)).format('YYYY-MM-DD HH:mm:ss')
	  }
	}
  }
</script>

<style>
	/deep/ .el-table td, .el-table th.is-leaf{
		padding: 20px 25px;
	}
	.el-breadcrumb{
		margin-bottom: 20px;
		font-size: 18px;
	}
	/deep/ .el-pagination{
		height: 10px;
	}
	
	/* 搜索栏样式 */
	.el-row {
	    margin-bottom: 20px;
	    &:last-child {
	      margin-bottom: 0;
	    }
	  }
	  .el-col {
	    border-radius: 4px;
	  }
	  .bg-purple-dark {
	    background: #ece7eb;
	  }
	  .bg-purple-light {
	    background: #ece7eb;
	  }
	  .grid-content {
	    border-radius: 4px;
	    min-height: 36px;
	  }
	  .row-bg {
	    padding: 10px 0;
	    background-color: #f9fafc;
	  }
	  
	  .el-col-title-action {
	  		background-color: #e9eef3;
	  }
	  .el-col-text-action {
	  		min-width: 200rpx;
	  		border-radius: 20rpx;
	  }
	  .el-col-button-action {
	  		width: 150rpx;
	  		border-radius: 20rpx;
	  }
	  .el-col-input-action {
	  		height: 70rpx;
	  		border-radius: 20rpx;
	  		color: #56585a;
	  		background-color: #ffffff;
	  }
</style>